@import url('https://fonts.googleapis.com/css?family=PT+Sans:400,400i,700,700i&subset=cyrillic,cyrillic-ext');

@mixin size($width, $height) { width: $width; height: $height;}
@mixin bg($bgi, $bgr, $bgs, $bgp) {background-image: $bgi; background-repeat: $bgr; background-size: $bgs; background-position: $bgp;}
@mixin font($ffamily, $fsize, $fweight) {font-family: $ffamily; font-size: $fsize; font-weight: $fweight;}

$orange: #ff7800;
$black: #282828;
$grey: #787878;
$ptSans: 'PT Sans', sans-serif;
body {
    overflow-x: hidden;
}
#map {
    width: 100%;
