.button {
    border: none;
    outline: none;
    border-radius: 6px;
    cursor: pointer;
    &:hover,
    &:focus {
        border: none;
        outline: none;
        background-color: darken($orange, 10%);
    }
    &-close {
        cursor: pointer;
        font-size: 50px;
        border: none;
        background-color: transparent;
        color: $black;
        &:hover,
        &:focus {
            color: $orange;
            outline: none;
        }
    }
    &-bar {
        cursor: pointer;
        padding: 0;
        margin: 0;
        margin-top: 18px;
        background: transparent;
        border: none;
        outline: none;
        &:after {
            content: url('../img/menu-button.svg');
            &:hover,
            &:focus {
                outline: none;
                border: none;
            }
        }
        &:hover,
        &:focus {
            outline: none;
            border: none;
        }
    }
    &-bar-close {
        cursor: pointer;
        padding: 0;
        margin: 0;
        margin-top: 18px;
        background: transparent;
        border: none;
        outline: none;
        &:after {
            content: url('../img/close-button.svg');
            &:hover,
            &:focus {
                outline: none;
                border: none;
            }
        }
        &:hover,
        &:focus {
            outline: none;
            border: none;
        }
    }
    &-lg {
        background-color: $orange;
        @media screen and (min-width: 320px) {
            @include size(280px, 100%);
            min-height: 60px;
            max-height: 120px;
        }
        @media screen and (min-width: 576px) {
            @include size(325px, 60px);
        }        
    }
    &-xl {
        background-color: $orange;
        @media screen and (min-width: 320px) {
            @include size(248px, 100%);
            min-height: 40px;
            max-height: 80px;
        }
        @media screen and (min-width: 576px) {
            @include size(400px, 40px);
            
        }
    }
    &-auto {
        background-color: $orange;
    }
}
